import numeral from 'numeral';
import React from 'react'

const ProductSalesTotalValueSummary = ({ productSalesSummaryData }) => {

    const minProgressValue = 0;
    const maxProgressValue = productSalesSummaryData.length > 0 ? productSalesSummaryData[0].salesTotalPrice + 10000 : 100;
    const progressValue = productSalesSummaryData.length > 0 ? (productSalesSummaryData[0].salesTotalPrice / maxProgressValue) * 100 : minProgressValue;

    return (
        <>
            <div className="col-xl-3 col-md-6">
                <div className="stats card l-bg-orange-dark">
                    <div className="card-statistic-3 p-4">
                        <div className="mb-4">
                            <h5 className="card-title mb-0">Product Sales Value</h5>
                        </div>
                        <div className="row align-items-center mb-2 d-flex">
                            <div className="col-8">
                                {productSalesSummaryData.length === 0 ? (
                                    <h2 className="d-flex align-items-center mb-0">₹ 0</h2>
                                ) : (
                                    productSalesSummaryData.map((value, index) => (
                                        <h2 className="d-flex align-items-center mb-0" key={index}>{`₹ ${numeral(value.salesTotalPrice).format('0,0')}`}</h2>
                                    ))
                                )}
                            </div>
                        </div>
                        <div className="progress mt-1" style={{ height: '8px' }}>
                            <div className="progress-bar l-bg-cyan" role="progressbar" aria-valuenow={progressValue} aria-valuemin={minProgressValue} aria-valuemax={maxProgressValue} style={{ width: `${progressValue}%` }} aria-label="product-sales-value-progress-bar"/>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProductSalesTotalValueSummary