import React from 'react'
import { Link } from 'react-router-dom'

const ReceivedOrdersIconsLabelLink = ({ receivedOrdersData }) => {
    return (
        <>
            {/* received orders icons with label link */}
            <Link className="position-relative ms-4 h6 sidebar-links" data-bs-toggle="modal" data-bs-target="#receivedOrders" style={{ color: 'grey' }}>
                <i className="bi bi-check2-all me-1" style={{ fontSize: '21px', color: 'blue' }}></i>
                <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-primary">
                    {receivedOrdersData.length}
                </span>
                Received Orders
            </Link>
        </>
    )
}

export default ReceivedOrdersIconsLabelLink