import numeral from 'numeral';
import React from 'react'

const ShippedItemsTotalValueSummary = ({ shippedItemsData }) => {

    const minProgressValue = 0;
    const maxProgressValue = shippedItemsData.length > 0 ? shippedItemsData[0].shippedTotalPrice + 10000 : 100;
    const progressValue = shippedItemsData.length > 0 ? (shippedItemsData[0].shippedTotalPrice / maxProgressValue) * 100 : minProgressValue;

    return (
        <>
            <div className="col-xl-3 col-md-6">
                <div className="stats card l-bg-blue-dark">
                    <div className="card-statistic-3 p-4">
                        <div className="mb-4">
                            <h5 className="card-title mb-0">Shipped Items Value</h5>
                        </div>
                        <div className="row align-items-center mb-2 d-flex">
                            <div className="col-8">
                                {shippedItemsData.length === 0 ? (
                                    <h2 className="d-flex align-items-center mb-0">₹ 0</h2>
                                ) : (
                                    shippedItemsData.map((value, index) => (
                                        <h2 className="d-flex align-items-center mb-0" key={index}>{`₹ ${numeral(value.shippedTotalPrice).format('0,0')}`}</h2>
                                    ))
                                )}
                            </div>
                        </div>
                        <div className="progress mt-1" style={{ height: '8px' }}>
                            <div className="progress-bar l-bg-green" role="progressbar" aria-valuenow={progressValue} aria-valuemin={minProgressValue} aria-valuemax={maxProgressValue} style={{ width: `${progressValue}%` }} />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ShippedItemsTotalValueSummary